.vue-notification-phase {
  font-family: $font-comfortaa;
  padding: 10px;
  margin: 0 5px 5px;

  font-size: 18px;

  color: black !important;
  background: $color-grey !important;
  //border-left: 5px solid #187FE7;


  &.warn {
    color: $color-state-warn-text !important;
    background: darken($color-state-warn-bg, 7) !important;
    //border-left-color: $color-state-warn-shadow;
  }

  &.error {
    color: $color-state-error-text !important;
    background: darken($color-state-error-bg, 7) !important;
    //border-left-color: $color-state-error-shadow;
  }

  &.success {
    color: $color-state-success-text !important;
    background: darken($color-state-success-bg, 7) !important;
    //border-left-color: $color-state-success-shadow;
  }
}


